.CheckoutSummary {
    text-align: center;
    width: 80%;
    margin: auto;
}

.ContactData {
    margin: 20px auto;
    width: 80%;
    text-align: center;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.12),
            0 2px 4px 0 rgba(0,0,0,0.08);
    border: 1px solid #eee;
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box; 
}

@media (min-width: 600px) {
    .ContactData {
        width: 500px;
    }
}
.Input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin: 8px;
}

.InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
    box-sizing: border-box;

}

.InputElement:focus {
    outline: none;
    background-color: #ccc;

}

.Invalid {
    border: 1px solid red;
    background-color: #FDA49A;

}
